import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { NzCodeBoxModule } from "app/showcase/share/nz-codebox/nz-codebox.module";
import { NzGridModule } from "app/showcase/share/nz-grid/nz-grid.module";
import { NzToolTipModule } from "app/showcase/share/nz-tooltip/nz-tooltip.module";
import { ExceptionSelectDemoComponent } from "app/showcase/custom-components/exception-select-demo/exception-select-demo.component";
import { ExceptionSelectDemoBasicComponent } from "app/showcase/custom-components/exception-select-demo/exception-select-demo-basic.component";
import { ExceptionSelectDemoRoutingModule } from "app/showcase/custom-components/exception-select-demo/exception-select-demo.routing.module";
import { ExceptionSelectModule } from "app/custom-components/custom-components.module";
import { AbnormalTypeService } from "app/custom-components/exception-select/abnormal-type.service";

@NgModule({
	declarations:[
		ExceptionSelectDemoComponent,
		ExceptionSelectDemoBasicComponent,
	],
	imports: [
		ExceptionSelectDemoRoutingModule,
		CommonModule,
		FormsModule,
		NzGridModule,
		NzToolTipModule,
		NzCodeBoxModule,
		ExceptionSelectModule
	],
	providers: [
		AbnormalTypeService
	]
})
export class ExceptionSelectDemoModule { }



